import React, { Component } from 'react'
import { Wrapper } from './Styled'

import MegaLogo from '../../../assets/megaresearch-white.svg'
import Facebook from '../../../assets/social/facebook-icon.svg'
import Linkedin from '../../../assets/social/linkedin-icon.svg'
import Mercurias from '../../../assets/mercurias.svg'
import axios from 'axios'

export default class Footer extends Component {

  constructor(props) {
    super(props)

    this.state = {
      name: '',
      email: '',
      message: '',
      success: false,
      loading: false
    }
  }


  submitForm = (e) => {
    console.log('send that shit')
    e.preventDefault()
    this.setState({
      loading: true
    }, () => {this.sendEmail()})
    
  }

  sendEmail = async() => {
    const data = {
      service_id: 'gmail',
      template_id: 'template_QvvyyyTl',
      user_id: 'user_dfpmoloiIfg3QWP3aWtJK',
      template_params: {
        from_name: this.state.name,
        from_email: this.state.email,
        from_message: this.state.message
      }
    }

    const res = await axios.post("https://api.emailjs.com/api/v1.0/email/send", data)
    console.log(res.status)

    this.setState({
      loading: false
    })

    if (res.status === 200) {
      this.setState({
        success: true
      })
    }
  }

  onNameChange = (e) => {
    this.setState({
      name: e.target.value
    })
  }

  onEmailChange = (e) => {
    this.setState({
      email: e.target.value
    })
  }

  onMessageChange = (e) => {
    this.setState({
      message: e.target.value
    })
  }


  render() {
    // user_dfpmoloiIfg3QWP3aWtJK
    // template_QvvyyyTl
    return (
      <div className='pure-g'>
        <div className='pure-u-1'>
          <Wrapper>
          <div className='pure-u-1'>
              <div className='contact-form'>
                <div className='contact-form-title'>¡Escribinos!</div>
                {this.state.success ? (
                  <div>
                    <p>Mensaje enviado!</p>                    
                  </div>
                ) : (
                  <form className='contact-form-body' onSubmit={this.submitForm}>
                    <div>
                      <input className='contact-form-input' onChange={this.onNameChange} required type="text" name="name" placeholder="Nombre"/>
                      <input className='contact-form-input' onChange={this.onEmailChange} required type="email" name="_replyto" placeholder="Email"/>
                    </div>                  
                    <textarea className='contact-form-message' onChange={this.onMessageChange} required name="message" placeholder="Mensaje: (Opcional)"/>
                    <input type="hidden" name="_next" value="/" />
                    <input className='contact-form-button' disabled={this.state.loading} type="submit" value="Enviar" />
                  </form>
                )}                
              </div>
            </div>
            <div className='pure-u-1'>
              <div className='footer'>
                <div className='upper'>
                  <div className='logo'>
                    <img src={MegaLogo} alt='megaresearch-white'/>
                    <div className='social'>
                      <a target="_blank" href='https://www.facebook.com/megaresearch2?ref=hl' rel="noopener noreferrer">
                        <img src={Facebook} alt='facebook-logo'/>
                      </a>                      
                      <a target="_blank" href="https://www.linkedin.com/company/mega-research" rel="noopener noreferrer">
                        <img src={Linkedin} alt='linkedin-logo'/>
                      </a>                               
                    </div>
                  </div>
                </div>                
                <div className='lower'>
                  <div className='copyright'>
                    Copyright ©2018 MEGARESEARCH. Todos los Derechos Reservados.
                  </div>
                  <div className='mercurias'>
                    <div>Diseñado y desarrollado por</div>
                    <img src={Mercurias} alt='mercurias'/>
                  </div>
                </div>
              </div>
            </div>
          </Wrapper>
        </div>
      </div>      
    )
  }
}


